html {
  font-family: Poppins, sans-serif;
}

body {
  margin: 0;
}

.header {
  background-image: url("Cover_photo.bced6dda.png");
}

.header__nav--content {
  grid-template-columns: repeat(2, 5vh);
  align-items: center;
  padding-top: 1vh;
  display: grid;
}

.header__contacts {
  padding-top: .5vh;
  text-decoration: none;
}

@media (width >= 744px) {
  .header__contacts {
    margin-right: 40px;
  }
}

@media (width >= 1024px) {
  .header__contacts {
    padding-top: 0;
    padding-bottom: 1vh;
  }

  .header__contacts:before {
    content: "+1 234 555-55-55";
    opacity: 0;
    text-align: right;
    letter-spacing: 2px;
    text-transform: uppercase;
    pointer-events: none;
    color: #fff;
    width: max-content;
    padding-right: 12px;
    font-size: 12px;
    font-weight: 700;
    transition: all .6s;
    display: block;
    position: relative;
    top: 50%;
    transform: translate(-100%, 100%);
  }

  .header__contacts:hover:before {
    opacity: 1;
    pointer-events: all;
  }
}

.header__sidebar {
  background-image: url("Vector_sidebar.a3727caf.svg");
  transition: all .2s;
}

.header__sidebar:hover {
  background-image: url("icon-vector-hover.13e539ab.svg");
}

.title {
  color: #fff;
